import React from 'react';
// Terra
import { LCDClient } from '@terra-money/terra.js';
import { useWallet } from '@terra-money/wallet-provider';

// Anchor
import { AnchorEarn, Account, CHAINS } from '@anchor-protocol/anchor-earn';
import {
  columbus5,
  bombay12,
  AddressProviderFromJson,
} from '@anchor-protocol/anchor.js';
const QueriesContext = React.createContext();

export function useQueries() {
  return React.useContext(QueriesContext);
}
const QueriesProvider = ({ children }) => {
  const { network, status } = useWallet();
  const currentNetwork = network.chainID === 'bombay-12' ? bombay12 : columbus5;
  const contractNetwork =
    network.chainID === 'bombay-12' ? 'bombay' : 'columbus';

  const lcd = new LCDClient({ URL: network.lcd, chainID: network.chainID });

  const addressProvider = new AddressProviderFromJson(currentNetwork);

  const account = new Account(CHAINS.TERRA);

  const userStatus = {
    initializing: status === 'INITIALIZING' ? true : false,
    connected: status === 'WALLET_CONNECTED' ? true : false,
    not_connected: status === 'WALLET_NOT_CONNECTED' ? true : false,
  };

  const anchorNetwork =
    network.chainID === 'bombay-12'
      ? { id: 1, network: bombay12 }
      : { id: 0, network: columbus5 };

  const anchorEarn = new AnchorEarn({
    chain: CHAINS.TERRA,
    network: anchorNetwork.id,
    address: account.accAddress,
  });

  return (
    <QueriesContext.Provider
      value={{
        lcd,
        account,
        userStatus,
        anchorEarn,
        anchorNetwork,
        currentNetwork,
        addressProvider,
        contractNetwork,
      }}
    >
      {children}
    </QueriesContext.Provider>
  );
};

export { QueriesProvider, QueriesContext };
