import React from 'react';
import styled from 'styled-components';
import { useApp } from '../context/AppContext';
import { useGas } from '../context/GasContext';
import { useToken } from '../context/TokenContext';
import textContext from '../textContext';

// Comps
import GasSelector from '../components/Asset/GasSelector';
import ErrorMessage from '../components/Asset/ErrorMessage';
import InvestButton from '../components/Asset/InvestButton';
import AssetStrategy from '../components/Asset/AssetStrategy';
import AssetSettings from '../components/Asset/AssetSettings';
import InvestAccordion from '../components/Asset/InvestAccordion';
import ErrorText from '../components/Asset/ErrorText';

// Material
import ChevronLeftIcon from '@mui/icons-material/ChevronLeft';
import CloseIcon from '@mui/icons-material/Close';

const Close = styled('span')`
  margin: 0rem !important;
  position: absolute !important;
  right: 15px !important;
  top: 20px !important;
  cursor: pointer;
  @media (max-width: 1199px) {
    right: initial !important;
    left: 20px !important;
    top: 7.5px !important;
  }
`;

export default function Asset(props) {
  React.useEffect(() => {
    window.scrollTo(0, 0);
  }, []);

  const { coins, getFees, selectGas, ancInvFee, deltaInvFee } = useGas();
  const {
    width,
    errMsg,
    balance,
    success,
    language,
    setErrMsg,
    setLoading,
    setSuccess,
  } = useApp();
  const { anchorApy } = useToken();
  const text = textContext[language].assets;
  const { investText, withdrawText } = text;
  const {
    position,
    close,
    currentValue,
    invest,
    withdrawValue,
    plus,
    img1,
    img2,
    data: { address, title, chain, minCollateral, long, short, breakdown },
  } = props;

  const defaultCol = (1.8 * Number(minCollateral) * 100).toFixed(0);

  const [amount, setAmount] = React.useState(currentValue ? currentValue : '');
  const [collateral, setCollateral] = React.useState(Number(defaultCol));

  const checkGas =
    coins && coins.findIndex((fil) => fil.denom === selectGas.name);
  const getGas =
    coins && coins.filter((fil) => fil.denom === selectGas.name)[0];

  const deltaFeeInit = deltaInvFee
    ? (Number(deltaInvFee.amount) / 1e6).toFixed(3)
    : 0;
  const anchorFeeInit = ancInvFee
    ? (Number(ancInvFee.amount) / 1e6).toFixed(3)
    : 0;

  const maxInvest =
    selectGas.name === 'uusd'
      ? Number(balance) - Number(anchorFeeInit)
      : Number(balance);
  const maxDeltaInvest =
    selectGas.name === 'uusd'
      ? Number(balance) - Number(deltaFeeInit)
      : Number(balance);

  const closeModal = () => {
    setSuccess(false);
    setLoading(false);
    setErrMsg();
    document.body.style.overflow = 'auto';
    close();
  };

  const handleCollateral = (event, newValue) => {
    setCollateral(newValue);
  };

  const handleModal = (e) => {
    e.stopPropagation();
    if (e.target.id === 'assetModal') {
      document.body.style.overflow = 'auto';
      setLoading(false);
      setAmount(0);
      setErrMsg();
      close();
    }
  };

  React.useEffect(() => {
    document.body.style.overflow = 'hidden';
  }, []);

  React.useEffect(() => {
    success &&
      setTimeout(() => {
        setSuccess(false);
        setAmount(0);
        setErrMsg();
        document.body.style.overflow = 'auto';
        getFees();
        close();
      }, 1500);
  }, [success, close, setSuccess]); // eslint-disable-line react-hooks/exhaustive-deps

  return (
    <div id="assetModal" onClick={(event) => handleModal(event)}>
      <div>
        <Close onClick={closeModal}>
          {width > 1199 ? <CloseIcon /> : <ChevronLeftIcon />}
        </Close>
        <h4 className="title">{invest ? investText : withdrawText}</h4>
        <hr />
        {errMsg && <ErrorMessage />}
        <div className="assetContainer">
          <AssetStrategy
            chain={chain}
            img1={img1}
            img2={img2}
            plus={plus}
            title={title}
          />
          <AssetSettings
            plus={plus}
            invest={invest}
            amount={amount}
            setAmount={setAmount}
            maxInvest={maxInvest}
            currentValue={currentValue}
            withdrawValue={withdrawValue}
            maxDeltaInvest={maxDeltaInvest}
          />
        </div>
        <GasSelector />
        {invest && (
          <InvestAccordion
            long={long}
            plus={plus}
            title={title}
            short={short}
            breakdown={breakdown}
            anchorApy={anchorApy}
            defaultCol={defaultCol}
            collateral={collateral}
            deltaFeeInit={deltaFeeInit}
            minCollateral={minCollateral}
            withdrawValue={withdrawValue}
            anchorFeeInit={anchorFeeInit}
            handleCollateral={handleCollateral}
          />
        )}
        {invest &&
          amount > 0 &&
          (plus ? maxDeltaInvest : maxInvest) - amount <= 1 && (
            <ErrorText error={text.warning} />
          )}
        {(checkGas === -1 ||
          (getGas.amount &&
            Number(getGas.amount) <
              (plus
                ? Number(deltaInvFee.amount)
                : Number(ancInvFee.amount)))) && (
          <ErrorText error={text.feeError} />
        )}
        <InvestButton
          plus={plus}
          getGas={getGas}
          amount={amount}
          invest={invest}
          address={address}
          position={position}
          checkGas={checkGas}
          collateral={collateral}
          deltaFeeInit={deltaFeeInit}
          network={props.data.network}
          withdrawValue={withdrawValue}
          anchorFeeInit={anchorFeeInit}
        />
      </div>
    </div>
  );
}
