import { useState } from 'react';
import { useApp } from '../../context/AppContext';
import * as style from '../../styles/layout/LaunchModal';
import Logo from '../../assets/images/logo.png';

let wrongCount = 0;

const handleInputChange = (e, setCode, setErrorMsg) => {
  const val = e.target.value;
  setCode(val);
  if (wrongCount > 4) {
    setErrorMsg('Excessive number of errors');
    return;
  }
  setErrorMsg('');
};

const handleConfirm = (
  code,
  errorMsg,
  setErrorMsg,
  setAccess,
  setHasAccess
) => {
  if (errorMsg) {
    return;
  }
  if (!code) {
    setErrorMsg('Please enter a code');
    return;
  }
  const accessCode = ['-_AxdxN3KQ$%gs3', 'PXh7ukc8wkT-kE4', 'AP3RTUR3'];
  const isPassed = accessCode.includes(code);
  if (!isPassed) {
    setErrorMsg('Wrong code');
    wrongCount++;
    if (wrongCount > 4) {
      setErrorMsg('Excessive number of errors');
    }
  } else {
    setAccess();
    setHasAccess(true);
  }
};

const LaunchModal = () => {
  const { setAccess, getAccess } = useApp();
  const [code, setCode] = useState('');
  const [errorMsg, setErrorMsg] = useState('');
  const [hasAccess, setHasAccess] = useState(getAccess);
  return (
    <style.LaunchModal hasAccess={hasAccess}>
      <style.ModalBg />
      <style.ModalContainer>
        <style.TopBg />
        <style.Logo src={Logo} alt="logo" />
        <style.Title>Welcome to our invite-only soft launch</style.Title>
        <style.Subtitle>
          Please enter your invitation code to continue.
        </style.Subtitle>
        <style.InputContainer>
          <style.Label>Code</style.Label>
          <style.Input
            error={errorMsg ? true : false}
            value={code}
            onInput={(e) => handleInputChange(e, setCode, setErrorMsg)}
            onKeyDown={(e) => {
              if (e.keyCode === 13)
                handleConfirm(
                  code,
                  errorMsg,
                  setErrorMsg,
                  setAccess,
                  setHasAccess
                );
            }}
          />
          {errorMsg && <style.ErrorMsg>{errorMsg}</style.ErrorMsg>}
          <style.Hint>
            This is an invitation-only "early access", for more details and how
            you can join, please visit{' '}
            <style.Link href="https://aperture.finance/">
              aperture.finance
            </style.Link>
            .
          </style.Hint>
        </style.InputContainer>
        <style.Btn
          disabled={errorMsg ? true : false}
          onClick={() =>
            handleConfirm(code, errorMsg, setErrorMsg, setAccess, setHasAccess)
          }
        >
          Continue
        </style.Btn>
      </style.ModalContainer>
    </style.LaunchModal>
  );
};

export default LaunchModal;
